import {NavLink} from 'react-router-dom'


const SignedOutLinks=()=>{
    return(
       <ul className='right'>
           <li><NavLink style={{color:'red'}} to='/SignUp'>SignUp</NavLink></li>
           <li><NavLink style={{color:'red'}} to='/SignIn'>Login</NavLink></li>
       </ul>
    )
    
       
}

export default SignedOutLinks